import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

let routes = [];

const routerContext = require.context("./", true, /index\.js$/);

routerContext.keys().forEach((route) => {
  // console.log(route)
  // 如果是根目录的 index.js、 不做任何处理
  if (route.startsWith("./index")) {
    return;
  }
  const routerModule = routerContext(route);
  // 兼容 import export 和 require module.export 两种规范 Es modules commonjs
  routes = [...routes, ...(routerModule.default || routerModule)];
});
// console.log(routes)

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

//路由卫士
router.beforeEach((to, from, next) => {
  // log.debug(localStorage.get("userInfo"))
  // console.log(router.app.$log)
  // router.app.$log.debug(router.app.$localStorage.get("userInfo"))
  // if (to.name !== 'Login' && (JSON.parse(router.app.$localStorage.get("userInfo")) || { name: "" }).name !== "xzy") next({ name: 'Login' })
  // else next()
  next();
});
export default router;
